import React from "react";
import BackIcon from "../assets/icons/BackIcon";
import OutlinedTrueIcon from "../assets/icons/OutlinedTrueIcon";
import CustomButton from "../components/CustomButton";

interface Iprops {
  onFormSubmit: React.FormEventHandler<HTMLFormElement>;
}
export default function Verified({ onFormSubmit }: Iprops) {
  return (
    <div style={{ minHeight: "calc(100vh - 300px)" }}>
      <form onSubmit={onFormSubmit} id="v">
        <div className="flex flex-col">
          <div className="self-center w-fit p-3 bg-[#ECFDF3] rounded-full">
            <div className="w-fit p-3 bg-[#D1FADF] rounded-full">
              <OutlinedTrueIcon />
            </div>
          </div>
          <p className="self-center text-3xl mt-3">Email verified</p>
          <p className="self-center mt-3 text-gray-500  text-base">
            Your accounnt has been verified successfully.
          </p>
          <p className="self-center text-gray-500 text-base">
            Click below to setup your store.
          </p>

          <div className="flex flex-col space-y-5 mt-8">
            <CustomButton buttonText="Continue" />

            <div className="rowBottom mt-8 flex flex-col justify-center items-center">
              <div className="flex justify-center mt-8">
                <button type="button" className="mr-1">
                  <BackIcon />
                </button>
                <p className="text-sm font-bold text-gray-500">
                  Back to log in
                </p>
              </div>
            </div>
          </div>
        </div>
      </form>
    </div>
  );
}
