import React from "react";

interface IButtonProps
  extends React.DetailedHTMLProps<
    React.ButtonHTMLAttributes<HTMLButtonElement>,
    HTMLButtonElement
  > {
  buttonText: string;
}
export default function CustomButton(props: IButtonProps) {
  return (
    <div>
      <button
        {...props}
        className="text-white bg-[#7244C8] hover:bg-[#4e2f88] focus:ring-4 focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5 me-2 mb-2 w-full"
      >
        {props.buttonText}
      </button>
    </div>
  );
}
