import React from "react";
import TrueIcon from "../assets/icons/TrueIcon";
import { Isteps } from ".";

interface Iprops {
  states: Isteps;
}
export default function Stepper({ states }: Iprops) {
  return (
    <div>
      <ul className="relative flex flex-row max-w-3xl mx-auto px-28">
        <li className="shrink basis-0 flex-1 group">
          <div className="min-w-[28px] min-h-[28px] w-full inline-flex items-center text-xs align-middle">
            <span
              className={`w-7 h-7 flex justify-center items-center flex-shrink-0 ${
                states.first ? "bg-none" : "bg-[#7244C8] ring-4 ring-[#F4EBFF]"
              }  font-medium text-gray-800 rounded-full `}
            >
              {states.first ? (
                <TrueIcon />
              ) : (
                <span className="rounded-full w-3 h-3 bg-white"></span>
              )}
            </span>
            <div
              className={` w-full h-0.5 flex-1 transition-colors ${
                states.first ? "bg-[#7244C8]" : "bg-gray-200"
              } group-last:hidden`}
            ></div>
          </div>
        </li>
        <li className="shrink basis-0 flex-1 group">
          <div className="min-w-[28px] min-h-[28px] w-full inline-flex items-center text-xs align-middle ">
            <span
              className={`w-7 h-7 flex justify-center items-center flex-shrink-0 ${
                states.first && !states.second
                  ? "bg-[#7244C8] ring-4 ring-[#F4EBFF]"
                  : !states.first && "bg-gray-100"
              }  font-medium text-gray-800 rounded-full `}
            >
              {states.first && states.second ? (
                <TrueIcon />
              ) : (
                <span className="rounded-full w-3 h-3 bg-white"></span>
              )}
            </span>
            <div
              className={`w-full h-0.5 flex-1 ${
                states.first && states.second ? "bg-[#7244C8]" : "bg-gray-200"
              }  group-last:hidden`}
            ></div>
          </div>
        </li>
        <li className="">
          <div className="min-w-[28px] min-h-[28px] w-full inline-flex  align-middle ">
            <span
              className={`w-7 h-7 flex justify-center items-center flex-shrink-0 ${
                states.second && !states.third
                  ? "bg-[#7244C8] ring-4 ring-[#F4EBFF]"
                  : (states.third || !states.second || !states.first) &&
                    "bg-gray-100"
              }  font-medium text-gray-800 rounded-full `}
            >
              {states.first && states.second && states.third ? (
                <TrueIcon />
              ) : (
                <span className="rounded-full w-3 h-3 bg-white"></span>
              )}
            </span>
            {/* <div className=" w-full h-px flex-1 bg-gray-200 group-last:hidden"></div> */}
          </div>
        </li>
      </ul>
      <div className="grid grid-cols-3  justify-items-center">
        <div className="mt-2 flex justify-center flex-col items-center">
          <p
            className={`${
              states.first ? "text-green-500" : "text-[#7244C8]"
            } text-[14px] font-bold`}
          >
            Your details
          </p>
          <p className="text-gray-500 text-[12px]">
            Provide your basic information
          </p>
        </div>
        <div className="mt-2 flex justify-center flex-col items-center">
          <p
            className={`${
              states.first && !states.second
                ? "text-[#7244C8]"
                : states.first && states.second && "text-green-500"
            } text-[14px] font-bold`}
          >
            Confirmation
          </p>
          <p className="text-gray-500 text-[12px]">Confirm your email</p>
        </div>
        <div className="mt-2 flex justify-center flex-col items-center">
          <p
            className={`${
              states.first && states.second && !states.third
                ? "text-[#7244C8]"
                : states.first &&
                  states.second &&
                  states.third &&
                  "text-green-500"
            } text-[14px] font-bold`}
          >
            Store information
          </p>
          <p className="text-gray-500 text-[12px]">Set your store main info</p>
        </div>
      </div>
    </div>
  );
}
