import React, { useRef } from "react";
import BackIcon from "../assets/icons/BackIcon";
import LetterIcon from "../assets/icons/LetterIcon";
import CustomButton from "../components/CustomButton";

interface Iprops {
  onFormSubmit: React.FormEventHandler<HTMLFormElement> | undefined;
}
export default function Otp({ onFormSubmit }: Iprops) {
  const firstInputRef = useRef<HTMLInputElement>(null);
  const secondInputRef = useRef<HTMLInputElement>(null);
  const thirdInputRef = useRef<HTMLInputElement>(null);
  const fourthInputRef = useRef<HTMLInputElement>(null);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.id;

    if (value === "1" && secondInputRef.current && e.target.value !== "") {
      secondInputRef.current.focus();
    } else if (
      value === "2" &&
      thirdInputRef.current &&
      e.target.value !== ""
    ) {
      thirdInputRef.current.focus();
    } else if (
      value === "3" &&
      fourthInputRef.current &&
      e.target.value !== ""
    ) {
      fourthInputRef.current.focus();
    } else if (
      value === "4" &&
      e.target.value === "" &&
      thirdInputRef.current
    ) {
      thirdInputRef.current.focus();
    } else if (
      value === "3" &&
      e.target.value === "" &&
      secondInputRef.current
    ) {
      secondInputRef.current.focus();
    } else if (
      value === "2" &&
      e.target.value === "" &&
      firstInputRef.current
    ) {
      firstInputRef.current.focus();
    }
  };
  return (
    <div style={{ minHeight: "calc(100vh - 300px)" }}>
      <form onSubmit={onFormSubmit}>
        <div className="flex flex-col">
          <div className="self-center w-fit p-3 bg-[#F9F5FF] rounded-full">
            <div className="w-fit p-3 bg-[#F4EBFF] rounded-full">
              <LetterIcon />
            </div>
          </div>
          <p className="self-center text-3xl mt-3">Check your email</p>
          <p className="self-center text-gray-500 mt-1 text-base">
            We sent a verification code to{" "}
          </p>
          <p className="self-center text-gray-500 mt-1 text-base">
            omarmouneer@gmail.com{" "}
          </p>

          <div className="flex flex-row items-center justify-between mx-auto w-full max-w-lg gap-2 mt-8">
            <div className="w-[80px] h-[80px] ">
              <input
                required
                maxLength={1}
                autoFocus
                onChange={handleInputChange}
                className="text-5xl text-[#7244C8] font-bold w-full h-full flex flex-col items-center justify-center text-center px-5 outline-none rounded-xl border border-gray-200  bg-white focus:ring-4 focus:ring-[#F4EBFF] focus:border-[#7244C8]"
                type="text"
                name=""
                id="1"
                ref={firstInputRef}
              />
            </div>
            <div className="w-[80px] h-[80px] ">
              <input
                required
                maxLength={1}
                className="text-5xl text-[#7244C8] font-bold w-full h-full flex flex-col items-center justify-center text-center px-5 outline-none rounded-xl border border-gray-200  bg-white focus:ring-4 focus:ring-[#F4EBFF] focus:border-[#7244C8]"
                onChange={handleInputChange}
                ref={secondInputRef}
                type="text"
                name=""
                id="2"
              />
            </div>
            <div className="w-[80px] h-[80px] ">
              <input
                required
                maxLength={1}
                className="text-5xl text-[#7244C8] font-bold w-full h-full flex flex-col items-center justify-center text-center px-5 outline-none rounded-xl border border-gray-200  bg-white focus:ring-4 focus:ring-[#F4EBFF] focus:border-[#7244C8]"
                onChange={handleInputChange}
                type="text"
                name=""
                id="3"
                ref={thirdInputRef}
              />
            </div>
            <div className="w-[80px] h-[80px] ">
              <input
                required
                maxLength={1}
                className="text-5xl text-[#7244C8] font-bold w-full h-full flex flex-col items-center justify-center text-center px-5 outline-none rounded-xl border border-gray-200  bg-white focus:ring-4 focus:ring-[#F4EBFF] focus:border-[#7244C8]"
                onChange={handleInputChange}
                type="text"
                name=""
                id="4"
                ref={fourthInputRef}
              />
            </div>
          </div>

          <div className="flex flex-col space-y-5 mt-8">
            <div>
              <CustomButton buttonText="Verify email" />
            </div>

            <div className="rowBottom mt-8 flex flex-col justify-center items-center">
              <p className="mt-2 text-sm text-gray-500 ">
                Didnt receive the email?
                <span>
                  <a className="text-[#7244C8] font-bold ml-1" href="/">
                    Click to resend
                  </a>
                </span>
              </p>
              <div className="flex justify-center mt-8">
                <button type="button" className="mr-1">
                  <BackIcon />
                </button>
                <p className="text-sm font-bold text-gray-500">
                  Back to log in
                </p>
              </div>
            </div>
          </div>
        </div>
      </form>
    </div>
  );
}
