import React, { ReactElement, useState } from "react";
import ArrowDownIcon from "../assets/icons/ArrowDownIcon";

export interface IselectProps {
  itemName: string;
  icon?: ReactElement;
}
interface Iprops {
  selectList: IselectProps[];
  lable: string;
  helperText?: string;
}
export default function Select({ selectList, lable, helperText }: Iprops) {
  const [show, setShow] = useState<boolean>(false);
  return (
    <div>
      <label
        htmlFor="location-search"
        className="mb-2 text-sm font-medium text-gray-600 "
      >
        {lable}
      </label>
      <div className="flex flex-col mt-1">
        <button
          id="dropdown-button-2"
          data-dropdown-toggle="dropdown-search-city"
          className=" w-full flex z-10 py-2.5 justify-between items-center px-4 text-sm font-medium text-center text-gray-500 bg-gray-50 border border-gray-300 rounded-lg hover:bg-gray-200 focus:ring-4 focus:outline-none focus:ring-gray-100 "
          type="button"
          onClick={() => setShow(!show)}
          onBlur={() => setShow(false)}
        >
          <div className="flex gap-2">
            {selectList[0]?.icon}
            <p>{selectList[0]?.itemName}</p>
          </div>
          <ArrowDownIcon />
        </button>
        {helperText && (
          <p className="mt-2 text-sm text-gray-500 ">{helperText}</p>
        )}
        <div
          id="dropdown-search-city"
          className={`absolute ${
            show ? "" : "hidden"
          } z-[1000] mt-10  bg-white divide-y divide-gray-100 rounded-lg shadow w-72`}
        >
          <ul
            className="py-2 text-sm text-gray-700 "
            aria-labelledby="dropdown-button-2"
          >
            {selectList.map((item, index) => (
              <li key={`${index + 1} li`}>
                <button
                  type="button"
                  className="inline-flex w-full px-4 py-2 text-sm text-gray-700 hover:bg-gray-100 "
                  role="menuitem"
                >
                  <div className="inline-flex items-center">
                    {item.icon}
                    {item.itemName}
                  </div>
                </button>
              </li>
            ))}
          </ul>
        </div>
      </div>
    </div>
  );
}
