import LangIcon from "../assets/icons/LangIcon";
import CustomButton from "../components/CustomButton";
import CustomInput from "../components/CustomInput";
import InputSelect from "../components/InputSelect";

interface Iprops {
  onFormSubmit: React.FormEventHandler<HTMLFormElement> | undefined;
}
export default function SignUpForm({ onFormSubmit }: Iprops) {
  return (
    <div className="formContent flex flex-col items-center justify-center self-stretch">
      <form
        className="w-full flex gap-2 flex-col"
        onSubmit={onFormSubmit}
        style={{ minHeight: "calc(100vh - 300px)" }}
      >
        <CustomInput
          lable="Name"
          id="name"
          required
          type={"text"}
          placeholder="Enter Your Name"
        />
        <CustomInput
          id="email"
          lable="Email"
          required
          type={"email"}
          placeholder="Enter Your Email"
        />
        <InputSelect />
        <CustomInput
          id="password"
          lable="Password"
          required
          type={"password"}
          placeholder="Create a Password"
          helperText="Must be at least 8 characters."
        />
        <CustomButton buttonText="Get started" type="submit" />
      </form>
      <div className="rowBottom  flex justify-center">
        <p id="helper-text-explanation" className="mt-2 text-sm text-gray-500 ">
          Already have an account?{" "}
          <span>
            <a className="text-[#7244C8] font-bold" href="/">
              Log in
            </a>
          </span>
        </p>
      </div>
      <div className="groupTop flex justify-center mt-2">
        <a className="text-gray-500" href="/">
          
        </a>
        <LangIcon />
      </div>
    </div>
  );
}
