import React, { useState } from "react";
import logo from "../assets/img/image 5.png";
import Stepper from "./Stepper";
import patternImage from "../assets/img/patern.png";
import StarIcon from "../assets/icons/StarIcon";
import StoreForm from "./StoreForm";
import Otp from "./Otp";
import Verified from "./Verified";
import SignUpForm from "./SignUpForm";
import VerifyMain from "../assets/icons/VerifyMain";
import ShopingIcon from "../assets/icons/ShopingIcon";
import SmallMailIcon from "../assets/icons/SmallMailIcon";
import StyledArrow from "../assets/icons/StyledArrow";

export interface Isteps {
  first: boolean;
  second: boolean;
  third: boolean;
}
export default function BasicForm() {
  const [veriy, setVerify] = useState<boolean>(false);
  const [stepsRange, setStepsRange] = useState<Isteps>({
    first: false,
    second: false,
    third: false,
  });

  const onFormSubmit = (event: any) => {
    console.log(stepsRange, "setStepsRange");
    event.preventDefault(); // This prevents the default form submission behavior
    if (stepsRange.first && !stepsRange.second && !stepsRange.third) {
      setStepsRange((prev) => ({ ...prev, second: true }));
    } else if (
      stepsRange.first &&
      stepsRange.second &&
      !stepsRange.third &&
      event.target.id === "v"
    ) {
      setVerify(true);
    } else if (
      stepsRange.first &&
      stepsRange.second &&
      !stepsRange.third &&
      event.target.id !== "v"
    ) {
      setStepsRange((prev) => ({ ...prev, third: true }));
    } else if (!stepsRange.first && !stepsRange.second && !stepsRange.third) {
      setStepsRange((prev) => ({ ...prev, first: true }));
    }
  };
  return (
    <div className="grid lg:grid-cols-2 md:grid-cols-1 sm:grid-cols-1 gap-2 box-border min-h-screen">
      <section className=" box-border">
        <div className="mt-2 ml-10">
          <img src={logo} alt="logo" />
        </div>
        <div className="mt-6">
          <Stepper states={stepsRange} />
        </div>
        <div className="container h-[76%] px-8 flex flex-col items-center">
          <div className="content flex flex-col mt-[70px] justify-center w-[360px]">
            <div className="hederForm w-full">
              {!stepsRange.first && !stepsRange.second && !stepsRange.third ? (
                <SignUpForm onFormSubmit={onFormSubmit} />
              ) : stepsRange.first &&
                !stepsRange.second &&
                !stepsRange.third ? (
                <Otp onFormSubmit={onFormSubmit} />
              ) : stepsRange.first &&
                stepsRange.second &&
                !stepsRange.third &&
                !veriy ? (
                <Verified onFormSubmit={onFormSubmit} />
              ) : (
                stepsRange.first &&
                stepsRange.second &&
                !stepsRange.third &&
                veriy && <StoreForm onFormSubmit={onFormSubmit} />
              )}
            </div>
          </div>
        </div>
        <div className="footer flex justify-between pr-8">
          <p className="text-gray-500 text-[14px] "> Markatty 2022</p>
          <p className="text-gray-500 text-[12px] flex">
            <span className="mr-2">
              <SmallMailIcon />
            </span>
            support@markatty.com
          </p>
        </div>
      </section>
      <section
        className="px-8 bg-cover bg-center relative lg:block md:hidden sm:hidden"
        style={{ backgroundImage: `url(${patternImage})` }}
      >
        <div className="absolute inset-0 bg-gradient-to-b from-[#7244C8] to-[#000000] opacity-90"></div>
        <div className="absolute inset-0 flex flex-col items-start ml-10 justify-center text-white px-10">
          <div className="self-start justify-center mb-4">
            {!stepsRange.first && !stepsRange.second && !stepsRange.third ? (
              <StarIcon />
            ) : (stepsRange.first && !stepsRange.second && !stepsRange.third) ||
              !veriy ? (
              <VerifyMain />
            ) : (
              ((stepsRange.first &&
                stepsRange.second &&
                !stepsRange.third &&
                veriy) ||
                stepsRange.third) && <ShopingIcon />
            )}
          </div>
          {!stepsRange.first && !stepsRange.second && !stepsRange.third ? (
            <>
              <h1 className="text-6xl mb-3">Start turning your</h1>
              <h1 className="text-6xl mb-6">ideas into reality.</h1>
            </>
          ) : (stepsRange.first && !stepsRange.second && !stepsRange.third) ||
            !veriy ? (
            <>
              <h1 className="text-6xl mb-3">Verify your</h1>
              <h1 className="text-6xl mb-6">account now</h1>
            </>
          ) : (
            ((stepsRange.first &&
              stepsRange.second &&
              !stepsRange.third &&
              veriy) ||
              stepsRange.third) && (
              <>
                <h1 className="text-6xl mb-3">Setup your store</h1>
                <h1 className="text-6xl mb-6">the way you like</h1>
              </>
            )
          )}

          <p className="text-lg">
            Create a free account and get full access to all features for
          </p>
          <p className="text-lg">
            30-days. No credit card needed. Get started in 2 minutes.
          </p>
        </div>
        <div className="absolute flex flex-col left-[-100px] top-[530px] justify-center text-white">
          <StyledArrow />
        </div>
      </section>
    </div>
  );
}
