import React from "react";

interface InputProps
  extends React.DetailedHTMLProps<
    React.InputHTMLAttributes<HTMLInputElement>,
    HTMLInputElement
  > {
  lable?: string;
  helperText?: string;
}
export default function CustomInput(props: InputProps) {
  return (
    <div className="w-full">
      {props.lable && (
        <label
          htmlFor={`${props.id}`}
          className="block mb-2 text-sm font-medium text-gray-600"
        >
          {props.lable}
        </label>
      )}
      <input
        {...props}
        className=" outline-none bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-4 focus:ring-[#F4EBFF] focus:border-[#7244C8] block w-full p-2.5 "
      />
      {props.helperText && (
        <p id={`${props.id}`} className="mt-2 text-sm text-gray-500 ">
          {props.helperText}
        </p>
      )}
    </div>
  );
}
